/* DarkModeToggle.css */
.theme-toggle {
  padding: 0.5rem;
  border: none;
  background: transparent;
  border-radius: 9999px;
  cursor: pointer;
  font-size: 1.25rem;
  transition: transform 0.2s;
}

.theme-toggle:hover {
  transform: scale(1.1);
}

.theme-toggle:focus {
  outline: none;
}
